body {
	line-height: 1.5;
	font-family: sans-serif;
	word-wrap: break-word;
	overflow-wrap: break-word;
	color:black;
	margin:2em;
}

h1 {

	font-size: 220%;
	font-weight: bold;
}

h2 {
	font-weight: bold;
	color: #005A9C;
	font-size: 140%;
	text-transform: uppercase;
}

p {
	margin: 1em 0;
}

pre {
	padding: 0px;
	border:0px;
	border-radius: 0px;
}

// red {
// 	color: red;
// }

a {
  color: #4183c4;
  font-weight: 300;
  text-decoration: none;
}

a:hover {
  color: #3d85c6;
  text-decoration: underline;
}

a#downloadLink {
  display: block;
  margin: 0 0 1em 0;
  min-height: 1.2em;
}

div#container {
  margin: 0 auto 0 auto;
  max-width: 720px;
  padding: 1em 1.5em 1.3em 1.5em;
}

h3 {
  border-top: 1px solid #eee;
  color: #666;
  font-size: 0.9em;
  font-weight: 500;
  margin: 20px 0 10px 0;
  padding: 10px 0 0 0;
  white-space: nowrap;
}

p#data {
  border-top: 1px dotted #666;
  font-family: Courier New, monospace;
  line-height: 1.3em;
  min-height: 6em;
  max-height: 1000px;
  overflow-y: auto;
  padding: 1em 0 0 0;
}

video {
  background: #222;
  margin: 10px auto;
  width: 320px;
  height: 240px;
}

#controls {
  display: flex;
  margin-top: 2rem;
  max-width: 28em;
}

button {
  flex-grow: 1;
  height: 3.5rem;
  min-width: 2rem;
  border: none;
  border-radius: 0.15rem;
  background: #005A9C;
  margin-left: 2px;
  box-shadow: inset 0 -0.15rem 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color:#ffffff;
  font-weight: bold;
  font-size: 1.5rem;
}
button:hover, button:focus {
  outline: none;
  background: #0474c4;
}
button::-moz-focus-inner {
  border: 0;
}
button:active {
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.2);
  line-height: 3rem;
}
button:disabled {
  pointer-events: none;
  background: lightgray;
}
button:first-child {
  margin-left: 0;
}